/* ============================================= */
/*              Grid System                      */
/* ============================================= */

.container {
  padding: 0 10px;

	@include mq(medium) {
		max-width: 1400px;
	  display: block;
	  margin-left: auto;
	  margin-right: auto;
	}
}

.row {
  display: flex;
  flex-direction: column;
  padding: 1em 10px;
		@include mq(medium) {
			flex-direction: row;
		}
}

[class^=col] {
  margin-top: 10px;
  flex-basis: 100%;
  min-height: 25px;
  padding: 10px;
  border-radius: 5px;

	@include mq(medium) {
		display: flex;
		justify-content: center;
		margin-top: 0;
		align-items: center;

		& + [class^=col] {
			margin-left: 10px;
		}
		&.centered {
		  margin-left: auto;
		  margin-right: auto;
		}
		&.col-1 {
		  flex-basis: 8.33%;
		}
		&.col-2 {
			flex-basis: 16.67%;
		}
		&.col-3 {
			flex-basis: 25.00%;
		}
		&.col-4 {
			flex-basis: 33.33%;
		}
		&.col-5 {
			flex-basis: 41.67%;
		}
		&.col-6 {
			flex-basis: 50.00%;
		}
		&.col-7 {
			flex-basis: 58.33%;
		}
		&.col-8 {
			flex-basis: 66.67%;
		}
		&.col-9 {
			flex-basis: 75.00%
		}
		&.col-10 {
			flex-basis: 83.33%;
		}
		&.col-11 {
			flex-basis: 91.67%;
		}
		&.col-12 {
			flex-basis: 100%;
		}
	}
}

@each $i, $color in $gridThemes {
  .theme.col-#{$i} {
    background-color: $color;
  }
}

.card {
  flex-basis: 100%;
}
